.buttons {
  display: flex;
  flex-wrap: wrap;
}

.buttons__button {
  color: teal;
  background-color: transparent;
  border: none;
  margin: 0 0 0 20px;
  border-bottom: 1px solid transparent;
  transition: color 0.3s;
}

@media (max-width: 767px) {
  .buttons__button {
    flex-basis: 100%;
    margin: 10px;
  }
}

.buttons__button:hover {
  color: rgb(2, 185, 185);
  cursor: pointer;
}

.buttons__button:active {
  color: rgb(4, 218, 218);
}

.buttons__button:disabled {
  color: #ccc;
}

.buttons__button:disabled {
  cursor: not-allowed;
}
